<template>
  <xy-context :breadcrumb="[{ name: '', path: '' }, { name: '' }]" title="">
    <xy-title style="margin-top: unset;"></xy-title>
    <a-form :form="form" @submit="onSubmit">
      <a-form-item v-bind="middleLayout" label="">
        <a-input
          v-decorator="[
            'username',
            {
              rules: [{ required: true, message: '' }],
            },
          ]"
          placeholder=""
          :maxLength="20"
        />
      </a-form-item>
      <a-form-item v-bind="middleLayout" label="E-mail">
        <a-input
          v-decorator="[
            'email',
            {
              rules: [
                {
                  type: 'email',
                  message: 'E-mail ',
                },
                {
                  required: true,
                  message: ' E-mail!',
                },
              ],
            },
          ]"
          placeholder=" E-mail"
        />
      </a-form-item>
      <a-form-item v-bind="middleLayout" label="">
        <a-input
          v-decorator="[
            'password',
            {
              rules: [
                {
                  required: true,
                  message: '',
                },
              ],
            },
          ]"
          type="password"
          placeholder=""
        />
      </a-form-item>
      <a-form-item v-bind="middleLayout" label="">
        <a-radio-group
          v-decorator="[
            'gender',
            {
              rules: [
                {
                  required: true,
                },
              ],
              initialValue: 1,
            },
          ]"
        >
          <a-radio :value="1"></a-radio>
          <a-radio :value="2"></a-radio>
        </a-radio-group>
      </a-form-item>
      <a-form-item v-bind="buttonLayout">
        <a-button type="primary" htmlType="submit">
          
        </a-button>
      </a-form-item>
    </a-form>
  </xy-context>
</template>

<script>
export default {
  name: 'createDemo',
  data() {
    return {
      form: this.$form.createForm(this),
      middleLayout: {
        labelCol: { span: 8 },
        wrapperCol: { span: 8 },
      },
      buttonLayout: {
        wrapperCol: { span: 8, push: 8 },
      },
    };
  },
  methods: {
    onSubmit(e) {
      e.preventDefault();
      this.form.validateFields((err, values) => {
        if (err) {
          return;
        }
        this.$post('/create', values).then(() => {
          this.$message.success('');
          this.$router.replace('/biz/list');
        });
      });
    },
  },
};
</script>

<style scoped></style>
